import React, { Component } from 'react';
import './App.css';
import axios from 'axios';

// const axiosInstance = axios.create({
//     baseURL: 'http://jsonplaceholder.typicode.com'
// });

class App extends Component {

    state = {
        username: "",
        email: ""
    }

    // asyncFunction = async () => {
    //     await fetch('http://jsonplaceholder.typicode.com/posts')
    //         .then(res => res.json())
    //         .then(json => console.log(json));
    // }

    componentDidMount() {

        // 使用fetch来发送请求
        // this.asyncFunction();

        // Json Placeholder Fake online REST API for developers
        // axiosInstance.get('/posts')
        //     .then(res => console.log(res.data))
        //     .catch(err => console.log(err));

        console.log("componentDidMount");
        // 调用服务器的API（发送请求）
        axios.get('/GetUserName')
            .then(res => {
                console.log(res.data);
                this.setState({
                    username: res.data.username,
                    email: res.data.email
                });
            }).catch(err => console.log(err));
    }

    render() {

        let strHelloContents = "";
        if (this.state.username === "" || this.state.username === null) {
            strHelloContents = "Hello AWS !";
        } else {
            strHelloContents = "Hello " + this.state.username + " ! Your email: " + this.state.email;
        }

        return (
            <div>
                {strHelloContents}
            </div>
        );
    }
}

export default App;



